import React, {Component} from 'react';
import AboutItem from './AboutItem';
import AboutItem2 from './AboutItem2';

import img1 from '../assets/img/about/1.jpg';
import img2 from '../assets/img/about/2.jpg';
import img3 from '../assets/img/about/3.jpg';
import img4 from '../assets/img/about/4.jpg';


const about = [
    {date: '2009-2011',title:'Our Humble Beginnings', subtitle:'Illustration', image:img1},
    {date: 'March 2011',title:'An Agency is Born', subtitle:'Grraphic Design', image:img2},
    {date: 'December 2012',title:'Transition to Full Service', subtitle:'Identity', image:img3},
    {date: 'July 2014',title:'Phase Two Expansion', subtitle:'Branding', image:img4},
];

class Timeline extends Component {
    render() {
        return (
            <section id="about">
            <div className="container">
              <div className="row">
                <div className="col-lg-12 text-center">
                  <h2 className="section-heading text-uppercase">About</h2>
                  <h3 className="section-subheading text-muted">Lorem ipsum dolor sit amet consectetur.</h3>
                </div>
              </div>
              <div className="row">
                <div className="col-lg-12">
                  <ul className="timeline">
                  {about.map((service,index)=>{
                            if (index%2 >=1){
                                return <AboutItem2 {...service} key={index}/>}
                                else{
                                    return <AboutItem {...service} key={index}/>
                                }
                            })}

                    <li className="timeline-inverted">
                      <div className="timeline-image">
                        <h4>Be Part
                          <br />Of Our
                          <br />Story!</h4>
                      </div>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </section>
        )
    }
}

export default Timeline;